<template>
    <div class="loginWrap">
        <header>
            <router-link to="/home">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div>密码登录</div>
        </header>
        <div class="form">
            <section class="input_c">
                <input type="text" placeholder="账号" v-model="username">
            </section>
            <section class="input_c">
                <input :type="value ? 'text' : 'password'" placeholder="密码" v-model="password">
                <el-switch
                        v-model="value"
                        active-color="#13ce66"
                        inactive-color="#909399">
                </el-switch>
            </section>
            <section class="input_c">
                <input type="text" placeholder="验证码" v-model="capCode">
                <div>
                    <img :src="capUrl" alt="">
                    <div @click="changeCap">
                        <p>看不清</p>
                        <p>换一张</p>
                    </div>
                </div>
            </section>
        </div>
        <p class="tips">温馨提示：未注册过的账号，登录时将自动注册</p>
        <p class="tips">注册过的用户可凭账号密码登录</p>
            <div class="loginBtn" @click="loginAction">登录</div>

        <div class="alet_container  animated bounceIn" v-if="isShow1">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span class="tip_icon_s1"></span>
                    <span class="tip_icon_s2"></span>
                </div>
                <p class="tip_text">密码错误</p>
                <div class="confrim" @click="isShow1 = !isShow1">确认</div>
            </section>
        </div>
        <div class="alet_container  animated bounceIn" v-if="isShow2">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span class="tip_icon_s1"></span>
                    <span class="tip_icon_s2"></span>
                </div>
                <p class="tip_text">验证码错误</p>
                <div class="confrim" @click="isShow2 = !isShow2">确认</div>
            </section>
        </div>
        <div class="alet_container  animated bounceIn" v-if="isShow3">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span class="tip_icon_s1"></span>
                    <span class="tip_icon_s2"></span>
                </div>
                <p class="tip_text">请输入手机号/邮箱/用户名</p>
                <div class="confrim" @click="isShow3 = !isShow3">确认</div>
            </section>
        </div>
        <div class="alet_container  animated bounceIn" v-if="isShow4">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span class="tip_icon_s1"></span>
                    <span class="tip_icon_s2"></span>
                </div>
                <p class="tip_text">请输入密码</p>
                <div class="confrim" @click="isShow4 = !isShow4">确认</div>
            </section>
        </div>
        <div class="alet_container  animated bounceIn" v-if="isShow5">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span class="tip_icon_s1"></span>
                    <span class="tip_icon_s2"></span>
                </div>
                <p class="tip_text">请输入验证码</p>
                <div class="confrim" @click="isShow5 = !isShow5">确认</div>
            </section>
        </div>

        <router-link to="/forget">重置密码？</router-link>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data(){
            return {
                value:false,
                username:"",
                password:"",
                capCode:"",
                capUrl:"",
                isShow1:false,
                isShow2:false,
                isShow3:false,
                isShow4:false,
                isShow5:false,
            }
        },
        created(){
            this.requestCode();
        },
        methods:{
            loginAction(){
                this.axios.post("/v2/login",{
                    username:this.username,
                    password:this.password,
                    captcha_code:this.capCode
                }).then(res=>{
                    console.log(res);
                    // if(localStorage.getItem('user_id')==null){
                    localStorage.setItem("user_id",res.user_id);
                    // }
                    localStorage.setItem("profile",JSON.stringify(res));

                    if (res.message == "用户名参数错误" || this.username == ""){
                        this.isShow3 = true;
                    } else if (res.message == "密码参数错误" ||this.password == ""){
                        this.isShow4 = true;
                    }else if (res.message == "验证码参数错误" || this.capCode == ""){
                        this.isShow5 = true;
                    } else if (res.message == "密码错误"){
                        this.isShow1 = true
                        this.changeCap();
                    }else if (res.message == "验证码不正确"){
                        this.isShow2 = true;
                        this.changeCap();
                    }else {
                        this.isShow1 = false;
                        this.isShow2 = false;
                        this.isShow3 = false;
                        this.isShow4 = false;
                        this.isShow5 = false;
                        this.$router.push("/profile");
                    }
                }).catch(err=>{
                    // console.log(err);
                })

            },
            requestCode(){
                this.axios.post("/v1/captchas").then(res=>{
                    this.capUrl = res.code
                    // console.log(res);

                }).catch(err=>{});
            },
            changeCap(){
                //请求验证码
                this.requestCode();
            }
        }
    }
</script>

<style scoped>
    a{
        text-decoration: none;
    }
    .loginWrap{
        padding-top: 1.95rem;
    }
    header>div {
        width: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        color: #fff;
        text-align: center;
        font-size: .8rem;
        font-weight: 700;
    }
    header>a{
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
        color: #fff;
    }
    .form{
        background-color: white;
        margin-top: .6rem;
    }
    .input_c{
        display: flex;
        justify-content: space-between;
        padding: .6rem .8rem;
        border-bottom: 1px solid #f1f1f1;
    }
    .input_c input{
        font-size: .7rem;
        color: #666;
        border: none;
        outline: none;
    }
    .input_c>div{
        display: flex;
        align-items: center;
    }
    .input_c>div p{
        font-size: .55rem;
             color: #666;
    }
    .input_c>div p+p {
        color: #3b95e9;
        margin-top: .2rem;
    }
    .input_c>div img{
        width: 3.5rem;
        height: 1.5rem;
        margin-right: .2rem;
    }
    .tips{
        font-size: .5rem;
        color: red;
        padding: .4rem .6rem;
        line-height: .5rem;
    }
    .loginBtn{
        margin: 0 .5rem 1rem;
        font-size: .7rem;
        color: #fff;
        background-color: #4cd964;
        padding: .5rem 0;
        border: 1px;
        border-radius: .15rem;
        text-align: center;
    }
    .loginWrap>*:last-child {
        float: right;
        font-size: .6rem;
        color: #3b95e9;
        margin-right: .3rem;
        text-decoration: none;
    }
    .alet_container{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 200;
    }
    .tip_text_container{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .tip_icon{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .tip_icon_s1{
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .tip_icon_s2{
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .tip_text{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .confrim{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
</style>